body {
  background-color: #9266ff;
}
.App {
  background-color: #3f3160;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}
.input {
  background-color: aqua;
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color: aliceblue;
}

input::placeholder {
  color: rgba(255, 255 255, 0.5);
}
button {
  height: 50px;
  width: 80px;
  border: 0;
  background-color: rgb(202, 156, 24);
  cursor: pointer;
  font-weight: bolder;
  color: #fff;
}

.list {
  color: #fff;
  width: 300px;
  background-color: #c717a1;
  border-radius: 5px;
}
.list p {
  padding: 10px;
  position: relative;
}
.list p span {
  position: absolute;
  right: 10px;
}
.faicons {
  margin: 0 5px;
  font-size: 15px;
}
.list p input {
  background-color: transparent;
  border: 0;
  color: #fff;
  font-size: 18px;
}
.list p input:focus {
  outline: none;
}
